.popup {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    z-index: 2;
    align-items: center;
    justify-content: center;
    visibility: hidden;
    transition: ease-in 0.5s;
    opacity: 0;
}